[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
}
[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: #283645;
}
[type="radio"]:checked + label::before,
[type="radio"]:not(:checked) + label::before {
  content: " ";
  position: absolute;
  left: -12px;
  top: -10px;
  width: 37px;
  height: 37px;
  border: 1px solid #E5E5E5;
  border-radius: 100%;
  background: #FFFFFF;
}
.form-block-radio__items + label:hover::before {
  border: 2px solid #283645;
}
.form-block-radio__items:focus + label::before {
  border: 2px solid #283645;
}
[type="radio"]:checked + label::after,
[type="radio"]:not(:checked) + label::after {
  content: " ";
  width: 15px;
  height: 15px;
  background: #D22856;
  position: absolute;
  top: 3px;
  left: 0;
  border-radius: 100%;
  transition: all 0.2s ease;
}
[type="radio"]:not(:checked) + label::after {
  opacity: 0;
  transform: scale(0);
}
[type="radio"]:checked + label::after {
  opacity: 1;
  transform: scale(1);
}
