.contact-information__block {
  margin-bottom: 22px;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: center;
    margin-top: 6px;
  }
}

.contact-information__block-question {
  font-style: normal;
  font-weight: 300;
  font-size: @big-font;
  line-height: 120%;
  text-align: center;
  color: @dark-blue;
  background-color: @white;
  margin: 0;
  padding-bottom: 15px;

  @media (min-width: @tablet-width) {
    font-size: @super-super-big;
    line-height: 54px;
  }

  @media (min-width: @desktop-width) {
    font-size: @extra-big;
    line-height: 68px;
  }
}

.contact-information__communication-block-action {
  font-style: normal;
  font-weight: bold;
  font-size: @base-font;
  line-height: 25px;
  text-align: center;
  color: @dark-blue;
  margin-bottom: 0;
  margin-top: 19px;

  &--margin {
    margin-top: -7px;
  }

  @media (min-width: @tablet-width) {
    margin-top: 0;
    margin-left: 9px;
    margin-bottom: 3px;

    &--margin {
      margin-left: -8px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-left: 157px;
    margin-top: 5px;

    &--guest {
      margin-left: 5px;
    }

    &--margin {
      margin-right: 158px;
      margin-left: 0;
    }
  }
}

.contact-information__communication-block-details {
  font-style: normal;
  font-weight: 300;
  font-size: @big-font;
  line-height: 33px;
  text-align: center;
  color: @fuchsia;
  margin-top: 0;

  &:hover {
    color: @dark-blue;
  }

  &:active {
    color: @dark-blue;
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    padding-top: 0;

    &--margin {
      margin-left: -8px;
    }

    &--margin-left {
      margin-left: 9px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-top: 21px;

    &--margin-left {
      margin-left: 159px;
      margin-top: 21px;
    }

    &--margin {
      margin-left: -159px;
    }
  }
}

.contact-information {
  display: flex;
  flex-direction: column;
  margin-right: auto;
  margin-left: auto;
  margin-top: 41px;
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    margin: 0;
    margin-top: 105px;
    margin-bottom: 11px;
    margin-left: 0;
    margin-right: 49px;
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    margin-top: 53px;
    margin-bottom: 20px;
  }
}

.contact-information__general {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
  }
}

.contact-information__communication-block {
  margin-top: -3px;

  @media (min-width: @tablet-width) {
    width: 50%;

    &--margin {
      margin-top: 18px;
      margin-left: -5px;
    }
  }

  @media (min-width: @desktop-width) {
    &--order {
      order: 1;
    }

    &--margin {
      margin-top: -3px;
    }
  }
}

.contact-information__communication-block-triangle {
  margin-top: 5px;
  position: absolute;
  top: 3217.75px;

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.contact-information__communication-block-trianglesecond {
  @media (min-width: @mobile-width-only) {
    display: none;
  }

  @media (min-width: @tablet-width) {
    display: block;
    position: absolute;
    top: 3178.75px;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.contact-information__communication-block-trianglethird {
  @media (min-width: @mobile-width-only) {
    display: none;
  }

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: block;
    position: absolute;
    top: 3537.75px;
  }
}
